.hero {
    position: relative;
    width: 100%;
    height: 100vh;
}

.hero__img {
    width: 100%; 
    height: 100%;
    overflow: hidden;
    object-fit: cover;
    position: absolute;
}

.hero__title {
    position: absolute;
    bottom: 15%;
    right: 65%;
    color: crimson;
    font-size: 4rem;
    padding: 15px;
}

@media screen and (max-width: 800px) {
    .hero__title {
        position: absolute;
        bottom: 15%;
        right: 20%;
        color: crimson;
        font-size: 4rem;
        padding: 15px;
    }
}